import React, { Component } from "react";

// import { Fade } from "react-reveal";

// import SkillData from "./data/SkillData";

import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

import profile from "../img/longProfile.jpg";

import { Card, Container, Image } from "semantic-ui-react";

import { AiOutlineHeart } from "react-icons/all";

import suit_profile from "../img/SuitProfile.jpg";

import CollectorCard from "./CollectorCard";

// https://codepen.io/drehimself/pen/QNXpyp

export class Me extends Component {
  constructor(props) {
    super(props);

    this.state = {
      navBarHeight: 0,
    };
  }

  componentDidMount() {
    this.setState({
      navBarHeight: document.getElementById("navbar").clientHeight,
    });
  }

  render() {
    const darkMode = this.props.dm;
    const textColor = this.props.dm ? "text-light" : "text-dark";
    const bgColor = this.props.dm ? "bg-dark" : "bg-light";
    const letterInLGBT = [
      {
        l: "L",
        c: "pink",
      },
      {
        l: "G",
        c: "orange",
      },
      {
        l: "B",
        c: "green",
      },
      {
        l: "T",
        c: "yellow",
      },
      {
        l: "+",
        c: "blue",
      },
    ];

    return (
      <Container
        className={`h-100 w-100 ${textColor}`}
        style={{
          paddingTop: this.state.navBarHeight,
          paddingBottom: this.state.navBarHeight / 2,
          overflowY: "auto",
        }}
      >
        <div className="row-wrapper">
          <CollectorCard
            topText="Waterpolo"
            bottomText="Coach"
            subText={
              <span>
                I coach a{" "}
                {letterInLGBT.map((item, index) => (
                  <span style={{ color: `var(--${item.c})`, fontWeight: 600 }}>
                    {item.l}
                  </span>
                ))}{" "}
                team.
              </span>
            }
            profile_image={suit_profile}
          />
          <CollectorCard />
          <CollectorCard />
          <CollectorCard />
        </div>
        <CollectorCard />
      </Container>
    );
  }
}

export default Me;
