import React from "react";

import profile from "../img/longProfile.jpg";

import { AiOutlineHeart } from "react-icons/all";

function CollectorCard(props) {
  const profile_image = props.profile_image ? props.profile_image : profile;
  const width = props.width ? props.width : 275;
  const height = props.height ? props.height : 400;
  const avatarWidth = props.avatarWidth ? props.avatarWidth : 50;
  const avatarHeight = props.avatarHeight ? props.avatarHeight : 50;
  const topText = props.topText ? props.topText : "Theo";
  const bottomText = props.bottomText ? props.bottomText : "Clapperton";
  const subText = props.subText ? props.subText : "Waterpolo Coach";
  const randomGradientNum = Math.floor(Math.random() * 5) + 1;
  const little_icon = props.little_icon ? (
    props.little_icon
  ) : (
    <AiOutlineHeart />
  );
  const little_icon_size = props.little_icon_size ? props.little_icon_size : 30;
  const card__style = {
    height: height,
    width: width,
    minHeight: height,
    minWidth: width,
    margin: 20,
    position: "relative",
  };
  const outer__card_style = {
    position: "absolute",
    minWidth: width - 35,
    minHeight: height - 35,
    top: 0,
    left: 0,
    borderRadius: 15,
    zIndex: 9,
    backgroundImage: `var(--gradient-${randomGradientNum})`,
  };
  const inner__card_style = {
    boxShadow: "2px 5px 25px rgba(0, 0, 0, .15)",
    position: "absolute",
    minWidth: width - 20,
    minHeight: height - 100,
    bottom: 0,
    right: 0,
    borderRadius: 15,
    zIndex: 9,
  };
  const avatar__image_style = {
    border: "25px",
    backgroundImage: `url(${profile_image})`,
    backgroundPosition: "center",
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
    borderStyle: "solid",
    borderWidth: "1px",
    borderColor: "black",
    borderRadius: 50,
    height: avatarHeight,
    width: avatarWidth,
    position: "absolute",
    top: `-${avatarHeight / 2}px`,
    left: `${avatarWidth / 4}px`,
  };
  return (
    <div className="" style={card__style}>
      <div className="outer__card" style={outer__card_style}>
        <div
          style={{
            position: "absolute",
            top: 7,
            right: 7,
            fontSize: little_icon_size,
          }}
        >
          {little_icon}
        </div>
      </div>
      <div className="bg-light inner__card" style={inner__card_style}>
        <div className="bg-danger avatar__image" style={avatar__image_style} />
        <div
          className="headingText__top"
          style={{ marginTop: 35, paddingLeft: 10 }}
        >
          <div
            style={{
              color: "black",
              fontWeight: "700",
              fontFamily: "var(--font-family-sans-serif)",
              fontSize: 38,
              paddingTop: 3,
            }}
            className=""
          >
            {topText}
          </div>
          <div
            style={{
              color: "black",
              fontWeight: "700",
              fontFamily: "var(--font-family-sans-serif)",
              fontSize: 38,
              paddingTop: 23,
            }}
            className=""
          >
            {bottomText}
          </div>
          <div
            style={{
              color: "grey",
              fontWeight: "300",
              fontFamily: "var(--font-family-sans-serif)",
              fontSize: 19,
              paddingTop: 18,
            }}
            className=""
          >
            {subText}
          </div>
        </div>
      </div>
    </div>
  );
}

export default CollectorCard;
